export default {"name":"马黛茶","menus":[{"name":"什么是马黛茶？","content":[{"text":"世界上只有少数几种植物生产咖啡因，而阿根廷马黛茶则是其中之一。除了茶，咖啡，可可，可乐（或可乐）和瓜拉那，马黛茶还被用来生产含咖啡因的饮料。当准备作为制造茶样饮料时，马黛茶不含实际的茶叶。"},{"text":"马黛茶是南美原产的草本植物，用于制作阿根廷，巴拉圭，乌拉圭和巴西南部的国民饮料。事实上，在这些国家，马黛茶在咖啡中消耗6比1。"},{"text":"那么马黛茶在南美洲怎么变得如此受欢迎，阿根廷马黛茶是如何在我们的茶杯中喝光的呢？"}]},{"name":"马黛茶起源","content":[{"text":"马黛茶的起源充满了民间传说。由巴拉圭（瓜拉尼）和巴西南部（图皮）森林的土着人民发现，它被称为主要用于身体耐力的“神来之笔”。根据部落情况，草药是农业依赖和游牧以及战士为基础的生活方式的膳食主食。马黛茶也用于医药目的。土著人认为马黛茶是给予他们维持生命，增加活力和医治病人的礼物。作为灵性药草，马黛茶被用于崇拜，并经常用于宗教和其他重要的部落仪式。"},{"text":"阿根廷马黛茶的兴奋剂性质在阿根廷高加索人（或牛仔）中很受欢迎，他们在牛驾车或长时间收获期间啜饮马黛茶能量。在干旱或饥荒期间，马黛茶也被用来代替稀缺食物。这种“牛仔咖啡”在篝火周围的早晨和晚上被啜饮。随着饭菜的准备，一个共享的麝香配对的葫芦传遍了周围，啜饮着团契。"},{"text":"随着西班牙人开始殖民南美各国，他们亲眼目睹了这种本土饮料对土地人民的健康和能量利益。他们养成了习惯，迅速传播和推广药草，因为他们征服了大陆。与咖啡和可可不同的是，南美洲的其他作物种植了出口现金的欧洲人，而马黛茶则不是驯化物种，必须从树木的野生林中采收。欧洲人花了几个世纪才弄清楚如何将马黛茶植物变成可靠的作物。事实证明，“秘密”是，马黛茶种子只有在通过某些本地鸟类的消化道时才萌发。"}]},{"name":"马黛茶如何生长","content":[{"text":"马黛茶是冬青属家族的一种，叶子是常青树（在所有四季中都会出现叶子），并产生小果实浆果，并呈绿白色的花朵。叶子和嫩茎通常是手工挑选，然后在受控环境中干燥。它们有时会在木柴火上烧烤，然后才会被破坏或切割，以制作最终的草本食用。一些马黛茶伴侣甚至在雪松或其他木材中陈年一年或更久，以增加口味。"},{"text":"最高质量的马黛茶伴侣在丰富的热带雨林树冠下避光生长，避免阳光直射。这个遮蔽物允许叶子比暴露在阳光下的植物保留更多的营养和风味。野生阿根廷马黛茶茶树每两年只收获一次，因为它们的叶子需要更长时间才能完全发育。鉴于栽培树木在每个冬季结束时都会收获，因为仔细修剪，阳光照射和控制水分，它们的生长速度更快。"},{"text":"马黛茶种植和加工遍布南美洲。今天，巴西是最大的草药生产国，其次是阿根廷和巴拉圭。"}]},{"name":"如何泡马黛茶","content":[{"text":"马黛茶最传统的做法是用葫芦制作。虽然原始的葫芦是由空心干燥的南瓜制成的小碗或杯子，但现代葫芦可用金属，陶瓷或木头制成。马蹄草或“草本植物”被放置在葫芦中，摇动，碾碎或磨碎。然后将冷水一点一点地倒入葫芦中，以便有时间吸入草药并准备输液。然后将热水（或有时冷却但不沸腾）倒在草药上以填充葫芦。草药注入水中产生茶样饮料。"},{"text":"注入的马黛茶草药不会像茶或咖啡一样从水中过滤出来。相反，饮料通过“bombilla”，一种由银，铜或不锈钢制成的金属吸管进行啜饮，底部有小孔。bombilla既可以作为吸管也可以作为筛子，因此可以在药草留在葫芦中时啜饮液体。"},{"text":"传统上，马黛茶是在一个共享的葫芦中准备的； 这是喝马黛茶的社交和文化体验的一部分。在配偶仪式上，“cebador”或配偶准备者准备一个葫芦配偶，啜饮它以确保配偶光滑，然后在同一个葫芦中准备另一个配偶，以便从客人到客人。每位嘉宾啜饮一只葫芦配偶，并将其送回到cebador，为该圈子中的下一位嘉宾做好准备。这是一种友谊和款待的传统仪式。"},{"text":"虽然葫芦制备仍然是南美洲喝马黛茶的首选方法，但现代方法也适用。许多公司将营养茶叶作为凉茶销售，并以预先包装好的茶叶袋和散叶草药的形式出售。阿根廷马黛茶茶叶伴侣可以浸泡在茶杯，茶壶，法式咖啡机或咖啡壶中，类似于传统的茶。"}]},{"name":"马黛茶文化","content":[{"text":"当伴侣注入南美大陆的各种文化时，啜饮马黛茶的传统可因国而异。每个人都有自己的文化，围绕着准备和喝杯马黛茶。"},{"text":"巴西人称他们的马黛茶输注为“chimarro”，与邻国的队友相比，据说这是一种更温和，更顺畅的伴侣。巴西称配偶葫芦为“cuia”，稻草筛称为“bomba”。"},{"text":"在乌拉圭，当地人走着带着他们的伴侣和“termo”的街道，这是旅行伴侣饮料的热水瓶。他们可以很容易地找到他们沿途的热水站来补充他们的termo。"},{"text":"在巴拉圭，当地人经常使用配偶作为草药输液的基地。他们从当地的“yuyera”或草药中采集草药，并将草药或“yuyo”添加到他们准备好的饮料中，以获得健康或风味益处。伴侣输液通常作为冷饮被消费，巴拉圭人称其为“tereré”。Tereré可能用冷水或冰水或果汁冲泡。"},{"text":"在阿根廷，烤好的伴侣通常会在早餐时用甜甜糕点加糖，或者作为下午的咖啡店享用。"}]},{"name":"品尝马黛茶","content":[{"text":"马黛茶通常被描述为朴实，植物，草本和苦甜。想象一下新鲜的热带雨林地板，你可能会想象到马黛茶的味道。"},{"text":"但是，根据种植区域，种植习惯和酿造技术，马黛茶的味道可能会有微妙的变化。许多风格的马黛茶伴侣都含有嫩树枝和嫩枝，可以为茶叶增添木香味。如果植物晚收，它的单宁含量可能比年轻的植物高，这会产生更涩苦的草本植物。一些生产者烘烤叶子和树枝烤制的味道。其他人将收获的草本放在木质容器中陈化，以传递更多层次的森林风味。"}]},{"name":"在马黛茶中的咖啡因含量","content":[{"text":"马黛茶通常被描述为一种饮料，包含咖啡的能量，传统茶的光滑味道，凉茶的治疗成分和巧克力的感觉良好因素。事实上，马黛茶植物含有咖啡因，茶碱和可可碱，这些兴奋剂在咖啡，茶和巧克力中也有发现。"},{"text":"如同使用含咖啡因的植物制成的任何饮料一样，咖啡因含量可以根据植物品种，加工方式以及如何酿造草药而变化。一些马黛茶植物的味道，咖啡因水平和其他营养素的强度可能会有所不同，具体取决于它是男性还是女性植物； 雌性植物的味道更温和，咖啡因更低。"},{"text":"通常，马黛茶饮料的咖啡因含量通常高于红茶但低于咖啡。如果您正在观看咖啡因摄入量，请始终向您的茶叶供应商咨询您所购买的马黛茶伴侣的咖啡因量。"}]},{"name":"购买和储存马黛茶","content":[{"text":"妥善保存马黛茶将确保它尽可能保持新鲜。草药不会真的变坏，但如果它位于周围，暴露于过长的时间，它会变得陈旧。如果妥善存放在阴凉黑暗的地方，放在不透明的密闭容器中，远离光线和潮湿，远离咖啡和香料等可以浸透香草味的食品，阿根廷马黛茶可以持续一至两年应该使用或更换。"},{"text":"从一家有信誉的公司购买阿根廷马黛茶是一个好主意，它可以告诉你何时以及如何加工和包装马黛茶，以及提供存储提示，以延长草药的寿命。"}]},{"name":"马黛茶泡法","content":[{"text":"马黛茶可以有不同的理想酿造温度和浸泡时间，这取决于其种植方式和酿造方式。要求您的茶叶供应商酝酿特定于您购买的马黛茶伴侣的指示。在此期间，这里有几个一般的马黛茶酿造技巧："},{"text":"使用新鲜，纯净，冷的过滤水。泉水最好。"},{"text":"一般来说，马黛茶伴侣浸泡在热水中，而不是沸腾，温度与绿茶相似，温度在160到180度之间。这是一个沸腾的地方。"},{"text":"如果您没有带温度控制的电热水壶，请记住，在海平面水温为190度，沸腾温度为212度。高度每增加100英尺，沸腾温度就会下降一度。"},{"text":"如果你的马黛茶伴侣有特定的酿造建议，请使用这些建议。但每8盎司使用约2克散叶草药。一杯水是一个安全的赌注。"},{"text":"当它注入时，盖上你的马黛茶伴侣，以保持浸泡在容器中的所有热量。"},{"text":"在建议的输注时间后（我们将马黛茶伴侣揉搓4到5分钟），尝尝你的马黛茶伴侣，然后决定是否希望它再长一点。与传统茶相似，在热水中注入时间越长，马黛茶的味道就越涩苦。"},{"text":"马黛茶通常可以多次注入。每次输液都会显示更多层次的香草味道。"},{"text":"马黛茶可以很简单地享用，但用蜂蜜，龙舌兰或糖来酿造冲泡饮料，甚至用牛奶或奶油稀释它也是常见的，就像使用传统茶一样。有些人喜欢用另外的草药（如薄荷）来调味饮料。其他人则添加果汁或柠檬水来增甜和调味饮料。"}]}]}